.main_help_block {
	display: inline-block;
	float: right;
	position: relative;
	margin-top: 47px;
	margin-right: 113px;
	height: 443px;
	width: 503px;
	background-color: #FFF;
	box-shadow: 4px 4px 30px 0 rgba(128, 128, 128, 0.2);

	.main_help_block_title {
		margin-top: 40px;
		margin-left: 50px;
		margin-bottom: 39px;
		height: 26px;
		width: 109px;
		color: #333;
		font-family: 'Raleway-Bold', sans-serif;
		font-size: 20px;
		font-weight: bolder;
		line-height: 26px;
	}

	input[type=text],
	input[type=email],
	input[type=tel] {
		color: #BABABA;
		font-family: 'Raleway-Regular', sans-serif;
		margin-left: 49px;
		font-size: 14px;
		line-height: 16px;
		padding-bottom: 10px;
		width: 181px;
		border: none;
		border-bottom: 1px solid #D5D5D5;
		outline: none;
	}

	input[type=text]:nth-of-type(1) {
		width: 181px;
		padding-bottom: 10px;
	}

	input[type=text]:nth-of-type(2) {
		width: 403px;
		margin-top: 30px;
		margin-bottom: 19px;
	}

	input[type=email] {
		padding-bottom: 5px;
	}

	input[type=tel] {
		margin-left: 39px;
		padding-bottom: 5px;
	}
}

.main_help_block input::placeholder {
	color: #A5A5A5;
	word-spacing: 1px;
}

.main_help_block input:-ms-input-placeholder { /* Internet Explorer 10-11 */
	color: #A5A5A5;
}

.main_help_block input::-ms-input-placeholder { /* Microsoft Edge */
	color: #A5A5A5;
}

.form_money_amount {
	display: flex;
	background: #F3F3F3;
	position: absolute;
	top: 86px;
	left: 272px;

	span {
		font-family: "ProximaNova-Regular", sans-serif;
		font-size: 12px;
		display: inline-block;
		cursor: pointer;
		margin-left: 1.5px;
		padding: 15px 19px;
		color: #BABABA;

		&:hover {
			background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
			color: #fff;
			font-weight: bold;
		}
	}
}

.main_help_block label:nth-of-type(1),
.main_help_block label:nth-of-type(2) {
	height: 15px;
	width: 71px;
	color: #A5A5A5;
	font-family: "ProximaNova-Light";
	font-size: 12px;
	font-weight: bold;
	line-height: 15px;
	cursor: pointer;
	z-index: 100;

	&:hover {
		color: #fff;
	}
}

.main_help_block label:nth-of-type(1) {
	position: absolute;
	top: 279px;
	left: 115px;
}

.main_help_block label:nth-of-type(2) {
	position: absolute;
	top: 279px;
	right: 111px;
}

.btn {
	position: relative;
	height: 45px;
	width: 201px;
	font-family: "ProximaNova-Light",sans-serif;
	background: #F3F3F3;
	margin-top: 30px;
	text-align: center;
	outline: none;
	border: none;
	color: #A5A5A5;
	cursor: pointer;
	-webkit-appearance: none;
	-moz-appearance: none;

	&:hover,
	&:checked {
		background: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
	}
}

.form_single {
	float: left;
	margin-left: 50.5px;
}

.form_month {
	float: right;
	margin-right: 50.5px;
}

.main_help_block_tip {
	display: inline-block;
	color: #A1A1A1;
	height: 14px;
	min-width: 340px;
	margin-left: 52px;
	word-spacing: 1px;
	margin-top: 26px;
	color: #A5A5A5;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 12px;
	line-height: 14px;
}

.main_help_block_help {
	display: inline-block;
	float: left;
	position: relative;
	margin-left: 50px;
	margin-top: 37px;
	height: 16px;
	width: 76px;
	color: #E78B48;
	font-family: "Raleway-Bold", sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	cursor: pointer;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.main_help_block_help::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 86px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

.main_help_block label {
	position: absolute;
	bottom: 41px;
	right: 43px;
	color: #A5A5A5;
	height: 16px;
	width: 175px;
	color: #A5A5A5;
	font-family: 'Raleway-Regular', sans-serif;
	font-size: 14px;
	line-height: 16px;

	b {
		color: inherit;
		font-weight: normal;

		a {
			text-decoration: underline;
			color: #A5A5A5;
		}

		a::after {
			content: '*';
		}
	}
}

.main_help_block input[type='checkbox'] {
	width: 13px;
	height: 13px;
	margin-top: 37.5px;
	margin-left: 133px;
	display: block;
	float: left;
	position: relative;
	cursor: pointer;
	outline: 1px solid #BFBFBF;
	border: unset;
}

.main_help_block input[type='checkbox']:after {
	content: "";
	vertical-align: middle;
	text-align: center;
	line-height: 13px;
	position: absolute;
	cursor: pointer;
	height: 13px;
	width: 13px;
	left: 0;
	top: 0;
	font-size: 10px;
	background: #fff;
}

.main_help_block input[type='checkbox']:hover:after,
.main_help_block input[type='checkbox']:focus:after,
.main_help_block input[type='checkbox']:checked:hover:after,
.main_help_block input[type='checkbox']:checked:focus:after {
	background: #fff;
	content: '\2714';
	color: #E78B48;
}

.main_help_block input[type='checkbox']:checked:after {
	background: #fff;
	content: '\2714';
	color: #E78B48;
}

