		width: 350px !important;
		height: 599px !important;
		float: none;
		margin-top: 141px;
		margin-left: 20%;
		box-sizing: border-box;

		.main_help_block_title {
			height: 26px;
			width: 98px;
			color: #333;
			font-family: $mob-font-regular;
			font-weight: normal;
			font-size: 18px;
			line-height: 26px;
			margin-left: 22px;
		}

		input[type=text],
		input[type=email],
		input[type=tel] {
			width: 310px !important;
			margin-left: 21px;
			font-family: $mob-font-thin !important;
			font-weight: normal;
		}
	}

	input[type=text]:nth-of-type(1) {
		margin-bottom: 65px;
	}

	input[type=email] {
		padding-bottom: 5px;
		margin-bottom: 20px;
	}

	.main_help_block input::placeholder {
		color: #A5A5A5;
		word-spacing: 1px;
		letter-spacing: 0.3px;
	}

	.main_help_block input:-ms-input-placeholder { /* Internet Explorer 10-11 */
		color: #A5A5A5;
		word-spacing: 1px;
		letter-spacing: 1px;
	}

	.main_help_block input::-ms-input-placeholder { /* Microsoft Edge */
		color: #A5A5A5;
		word-spacing: 1px;
		letter-spacing: 1px;
	}

	.form_money_amount {
		top: 153px !important;
		left: 21px;
		font-family: $mob-font-regular;

		span {
			padding: 20px 40px;
		}
	}

	.main_help_block .btn {
		width: 154px;
		margin-top: 20px;
	}

	.form_single {
		margin-left: 21px;
	}

	.form_month {
		margin-top: 20px !important;
		margin-right: 21px;
	}

	.main_help_block label:nth-of-type(1) {
		position: absolute;
		top: 376px;
		left: 62px;
	}

	.main_help_block label:nth-of-type(2) {
		position: absolute;
		top: 377px;
		right: 63px;
	}

	.main_help_block label:last-of-type {
		bottom: 96px;
		right: 129px;
		font-family: $mob-font-thin;
		font-weight: none;
		letter-spacing: 1px;
		white-space: nowrap;
	}

	.main_help_block_tip {
		min-width: 100px;
		width: 300px;
		height: 32px;
		font-family: $mob-font-regular;
		font-weight: none;
		margin-top: 20px;
		margin-left: 22px;
		font-size: 12px;
		line-height: 16px;
		letter-spacing: 0.3px;
	}

	.main_help_block_help {
		margin-top: 86px;
		font-family: $mob-font-regular;
		font-weight: normal;
		margin-left: 21px;
	}

	.main_help_block input[type='checkbox'] {
		float: none;
		position: absolute;
		margin: unset;
		top: 489px;
		left: 21px;
	}
}

