@import "partials/page_header";

@import "partials/help_form";

//contains responsive markups for main, about us, our achievements sections

//MEDIA QUERY 320

@media screen and (max-width: 320px) {
	.our_achievements {
		height: 615px;
		margin-top: 190px;
		width: 100%;

		br {
			display: none;
		}
	}

	.our_achievements .our_achievements_title {
		height: 102px;
		width: 270px;
		color: #333333;
		font-family: $mob-font-regular !important;
		font-weight: normal;
		font-size: 24px;
		line-height: 34px;
		margin-left: 35px;
		margin-bottom: 60px;
	}

	.our_achievements .our_achievements_title::before {
		left: 0;
		top: 148px;
		height: 3px;
		width: 20px;
	}

	.our_achievements_carousel {
		width: 290px;
		height: 162px;
		margin: unset;
		margin: auto;

		.slick-dots {
			bottom: -310px;
			left: -3.8%;
		}
	}

	.our_achievements_carousel div {
		height: 147px;
		width: 250px;
		margin: auto;
		position: relative;
	}

	.our_achievements_carousel div span {
		display: inline-block;
		margin-top: 21px;
		margin-left: 0px;
		height: 66px;
		width: 230px;
		color: #333333;
		font-family: $mob-font-regular !important;
		font-size: 14px;
		font-weight: normal;
		line-height: 22px;
	}

	.our_achievements_carousel div span:nth-of-type(1) {
		width: 230px;
		margin-top: 21px;
	}

	.our_achievements_carousel div::before {
		top: 0px;
		height: 3px;
		width: 100%;
	}
}

