@font-face {
	font-family: "SFNS-Regular";
	src: url("../fonts/SFNS/San_Francisco_Display_Regular.otf") format("opentype");
}

@font-face {
	font-family: "SFNS-Bold";
	src: url("../fonts/SFNS/San_Francisco_Display_Bold.otf") format("opentype");
}

@font-face {
	font-family: "SFNS-Thin";
	src: url("../fonts/SFNS/San_Francisco_Display_Thin.otf") format("opentype");
}

@font-face {
	font-family: "ProximaNova-Regular";
	src: url("../fonts/ProximaNova/ProximaNova-Regular.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Regular";
	src: url("../fonts/Raleway/Raleway-Regular.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Bold";
	src: url("../fonts/ProximaNova/ProximaNova-Bold.otf") format("opentype");
}

@font-face {
	font-family: "Raleway-Bold";
	src: url("../fonts/Raleway/Raleway-Bold.ttf") format("truetype");
}

@font-face {
	font-family: "ProximaNova-Light";
	src: url("../fonts/ProximaNova/ProximaNova-Light.ttf") format("truetype");
}

body {
	width: 100%;
	overflow-x: hidden;
	margin: 0;
	padding: 0;
	background: #fff;
	color: #333;
	font-family: 'ProximaNova-Regular','ProximaNova-Bold','Raleway-Regular','Raleway-Bold', sans-serif;
	font-size: 14px;
	line-height: auto;
}

.container {
	width: 1367px;
	margin: 0 auto;
	max-width: 100%;
}

@media screen and (max-width: 1024px) {
	.header_height_increase {
		margin-bottom: 340px;
	}
}

$mob-font-regular: "SFNS-Regular", sans-serif;
$mob-font-bold: "SFNS-Bold", sans-serif;
$mob-font-thin: "SFNS-Thin", sans-serif;
$Bghover-color: linear-gradient(224.95deg, #F3AE43 0%, #E78B48 100%);
$arrowcolor: #E78B48;

.home_block {
	background: #FBFBFB;
	position: relative;
	height: 100vh;
}

.home_header {
	width: 1367px;
	max-height: 124px;

	.home_header_top {
		position: relative;
		height: 45px;
		width: 100%;
		background: #F3F3F3;
		color: #F2F2F2;

		img:nth-of-type(1) {
			position: absolute;
			bottom: 18px;
			left: 114px;
		}

		img:nth-of-type(2) {
			position: absolute;
			bottom: 19px;
			left: 283px;
		}
	}
}

.home_header_top_tel {
	display: inline-block;
	height: 15px;
	margin-left: 134px;
	margin-top: 15px;
	margin-right: 0;
	width: 120px;
	color: #A1A1A1;
	font-family: 'ProximaNova-Light', sans-serif;
	font-size: 12px;
	line-height: 15px;
}

.home_header_top_email {
	display: inline-block;
	height: 15px;
	margin-left: 50px;
	margin-top: 15px;
	width: 120px;
	color: #A1A1A1;
	font-family: 'ProximaNova-Light', sans-serif;
	font-size: 12px;
	line-height: 15px;
}

.home_header_top_help {
	display: inline-block;
	float: right;
	margin-right: 99px;
	margin-top: 15px;
	height: 15px;
	width: 262px;
	color: #A1A1A1;
	font-family: 'ProximaNova-Light', sans-serif;
	font-size: 12px;
	line-height: 15px;
}

.home_header_bottom {
	position: relative;
	min-width: 75px;
	display: block;

	.home_header_bottom_logo {
		position: absolute;
		top: 15px;
		left: 101px;
		height: 46.89px;
		width: 178px;
	}

	.home_header_bottom_nav {
		position: absolute;
		list-style-type: none;
		text-align: middle;
		left: 332px;
		top: 17px;
		min-width: 651px;
		color: #333;
		font-family: "Raleway-Regular", sans-serif;
		font-weight: 500;
		font-size: 14px;
		line-height: 16px;

		li {
			display: inline-block;
			margin-right: 16px;
			padding-right: 15px;

			a {
				text-decoration: none;
				color: #7C7C7C;

				sup {
					position: absolute;
					top: 1px;
					right: 4px;
					color: #E78B48;
					font-family: "Raleway-Regular", sans-serif;
					font-weight: bold;
				}
			}
		}
	}
}

.home_header_bottom_nav_trigger {
	display: none;
}

.home_header_bottom_nav li:nth-of-type(3) {
	margin-right: 19px;
}

.home_header_bottom_nav li:nth-of-type(4) {
	position: relative;
	margin-right: 16px;
}

.home_header_bottom_nav li:nth-of-type(5) {
	margin-right: 17px;
}

.home_header_bottom_nav li:nth-of-type(6) {
	margin-right: 19px;
}

.home_header_bottom_nav li:nth-of-type(7) {
	margin-right: 0px;
}

.home_header_bottom_languagepicker {
	position: absolute;
	top: 16px;
	left: 1034px;
	display: inline-block;
	list-style-type: none;
	color: #979797;
	font-family: 'Raleway-Regular', sans-serif;
	font-weight: 500;
	font-size: 14px;
	line-height: 16px;
	width: 41px;
	height: 16px;
	overflow: hidden;
	transition: all .3s ease;
	vertical-align: middle;

	&:hover {
		height: 100px;
	}

	a {
		text-decoration: none;
		color: #979797 !important;

		li {
			width: 25px;
			margin-bottom: 10px;
			position: relative;
			font-weight: normal;
		}

		li:hover {
			color: #E78B48;
		}

		li:last-of-type {
			font-weight: none;
		}

		li.ukr::after {
			content: '';
			position: absolute;
			top: 6px;
			left: 32px;
			width: 8px;
			height: 4px;
			color: #979797;
			background: url("../img/ui/arrow_down.png") no-repeat center;
		}
	}
}

.home_header_bottom_help {
	float: right;
	position: relative;
	margin-right: 136px;
	margin-top: 31px;
	height: 16px;
	width: 76px;
	color: #E78B48;
	font-family: "Raleway-Bold", sans-serif;
	font-size: 14px;
	font-weight: bold;
	line-height: 16px;
	cursor: pointer;

	a {
		text-decoration: none;
		color: inherit;
	}
}

.home_header_bottom_help::after {
	content: '';
	position: absolute;
	top: 2px;
	left: 86px;
	width: 12px;
	height: 13px;
	background: url("../img/svg/arrow_right.svg") no-repeat center;
}

// MEDIA QUERY 320

@media screen and (max-width: 320px) {
	body {
		width: 100%;
		overflow-x: hidden;
		margin: 0;
		padding: 0;
		background: #fff;
		color: #333;
		font-family: 'SFNS-Regular','SFNS-Bold','SFNS-Thin', sans-serif;
		font-size: 14px;
		line-height: auto;
	}

	.container {
		width: 320px;
		margin: 0 auto;
		overflow-x: hidden !important;
		max-width: 100%;
	}

	.home_header {
		width: 100%;
		height: 58px !important;
		display: inline-block;

		.home_header_top {
			display: none;
		}

		.home_header_bottom {
			height: 60px;
			width: 100%;
		}
	}

	.home_header_bottom_languagepicker,
	.home_header_bottom_help {
		display: none;
	}

	.home_header_bottom_logo {
		top: 10px !important;
		left: 3px !important;
		width: 153px !important;
		height: 40.3px !important;
	}

	.home_header_bottom .home_header_bottom_nav {
		display: none;
		top: 50px;
		left: -20px;
		width: 100%;
		font-family: $mob-font-regular;
		font-weight: normal;

		li {
			width: 100%;
			margin-bottom: 20px;
			border-bottom: 1px solid rgba(165, 165, 165, 0.47);
			padding-bottom: 10px;
		}

		sup {
			top: -10px;
			left: 65px;
		}
	}

	.home_header_bottom_nav_trigger {
		float: right;
		position: relative;
		display: block !important;
		height: 60px !important;
		width: 25.61px;
		background: #FBFBFB;
		margin-top: 20px;
		margin-right: 15.26px;
		cursor: pointer;

		span {
			display: inline-block;
			position: absolute;
			height: 1.71px;
			width: 25.61px;
			background: #424343 !important;
		}

		span:nth-of-type(1) {
			top: 0;
		}

		span:nth-of-type(2) {
			top: 7px;
		}

		span:nth-of-type(3) {
			top: 14px;
		}
	}

	.home_header_bottom_nav_trigger:hover {
		.bar1 {
			transform: rotate(-45deg) translate(-4px, -1px);
			transition: ease-in 0.3s;
		}

		.bar2 {
			opacity: 0;
		}

		.bar3 {
			transform: rotate(45deg) translate(-10px, -7px);
			transition: ease-in 0.3s;
		}
	}

	.home_header_nav_menu {
		position: relative;
		display: block;
	}

	.home_header_nav_menu:hover .home_header_bottom_nav,
	.home_header_nav_menu:focus .home_header_bottom_nav {
		display: block;
	}
}

